@font-face {
  font-family: "bloggersans";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/bloggersans.woff2") format("woff2");
  src: url("../fonts/bloggersans.woff") format("woff");
}

@font-face {
  font-family: "bloggersans";
  font-weight: 700;
  font-style: bold;
  src: url("../fonts/bloggersans-bold.woff2") format("woff2");
  src: url("../fonts/bloggersans-bold.woff") format("woff");
}

@font-face {
  font-family: "bloggersans";
  font-weight: 700;
  font-style: italic;
  src: url("../fonts/bloggersans-bolditalic.woff2") format("woff2");
  src: url("../fonts/bloggersans-bolditalic.woff") format("woff");
}

@font-face {
  font-family: "bloggersans";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/bloggersans-light.woff2") format("woff2");
  src: url("../fonts/bloggersans-light.woff") format("woff");
}

@font-face {
  font-family: "bloggersans";
  font-weight: 300;
  font-style: italic;
  src: url("../fonts/bloggersans-lightitalic.woff2") format("woff2");
  src: url("../fonts/bloggersans-lightitalic.woff") format("woff");
}

@font-face {
  font-family: "bloggersans";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/bloggersans-medium.woff2") format("woff2");
  src: url("../fonts/bloggersans-medium.woff") format("woff");
}
